<template>
    <div>
        <Row class="margin-top-10">
            <label class="margin-left-10" style="margin-right:8px;">用户名：</label>
            <Input v-model="table.query.username" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">资源商：</label>
            <Input v-model="table.query.name" placeholder="" clearable style="width: 200px" />
            <label class="margin-left-10" style="margin-right:8px;">时间区间：</label>
            <DatePicker type="daterange" placement="bottom-end" placeholder="Select date" style="width: 200px"></DatePicker>
            <Button class="margin-left-10" type="primary" @click="doQuery" icon="md-search">搜索</Button>
        </Row>
        <Row class="margin-top-10">
            <i-col>
                <Table :columns="table.col" :data="table.data"></Table>
                <div style="margin: 10px;overflow: hidden">
                    <div style="float: right;">
                        <Page :total="table.total" :show-sizer="true" placement="top" @on-page-size-change="changePageSize" @on-change="changePage"></Page>
                    </div>
                </div>
            </i-col>
        </Row>
        <Modal v-model="model" :mask-closable="false" :title="showModelTitle" :loading="loadingBtn" @on-cancel="detailCancel">
            <div style="height: 400px">
                <vue-scroll ref="detailVScroll">
                    <template>
                        <div>
                            <Divider>企业信息</Divider>
                            <CellGroup >
                                <Cell title="机构代码" :extra="vo.code"/>
                                <Cell  class="picCell">
                                    <span>logo</span>
                                    <OssUpload v-if="logoPic.length!==0" :files="logoPic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="false"/>
                                </Cell>
                                <Cell title="企业名称" :extra="vo.name"/>
                                <Cell title="法人" :extra="vo.legalPerson"/>
                                <Cell class="picCell">
                                    <span>营业执照</span>
                                    <OssUpload v-if="licensePic.length!==0" :files="licensePic" :size="1" :format="['jpg','jpeg','png']" accept="image/*" :disabled="false"/>
                                </Cell>
                                <Cell title="有效期" :extra="vo.licensePeriod"/>
                                <Cell title="注册资本" :extra="vo.registerCapital"/>
                                <Cell title="注册日期" :extra="vo.registerDate"/>
                                <Cell title="经营范围" :extra="vo.businessScope"/>
                                <Cell title="注册地址" :extra="addressStr"/>
                                <Cell title="办公地址" :extra="officeAddressStr"/>
                                <Cell title="状态" :extra="statusStr"/>

                            </CellGroup>
                        </div>
                        <div>
                            <Divider>管理员信息</Divider>
                            <CellGroup>
                                <Cell title="用户名" :extra="vo.user.username"/>
                                <Cell title="昵称" :extra="vo.user.nickname"/>
                                <Cell title="角色" :extra="vo.roleEntity.name"/>
                            </CellGroup>
                        </div>
                        <div>
                            <Divider>账户信息</Divider>
                            <CellGroup>
                                <Cell title="真实姓名" :extra="vo.banking.realName"/>
                                <Cell title="银行卡号" :extra="vo.banking.cardNumber"/>
                                <Cell title="开户行" :extra="vo.banking.bankType"/>
                                <Cell title="微信账号" :extra="vo.banking.alipayCode"/>
                                <Cell title="支付宝账号" :extra="vo.banking.wechatCode"/>
                            </CellGroup>
                        </div>
                    </template>
                </vue-scroll>
            </div>
            <div slot="footer">
                <Poptip confirm title="您确定审核通过这条数据？" class="auditButton" transfer placement="top" @on-ok="pass">
                    <Button type="primary" :loading="loadingBtn">审核通过</Button>
                </Poptip>
                <Poptip confirm title="您确定驳回这条数据？" class="auditButton" transfer placement="top" @on-ok="reject">
                    <Button type="warning" :loading="loadingBtn">驳回</Button>
                </Poptip>
            </div>
        </Modal>
    </div>

</template>
<style>
    .auditButton {
        padding-right:10px;
    }
    .picCell .ivu-cell-title{
        width:456px;
        height:127px;
    }
    .picCell .ivu-cell-link .ivu-cell-item .ivu-cell-main .ivu-cell-title>div{
        float: right;
    }
    .picCell .ivu-cell-link .ivu-cell-item .ivu-cell-main .ivu-cell-title>span{
        height: 30%;
        overflow: auto;
        margin: auto;
        position: absolute;
        top: 0; left: 16px; bottom: 0; right: 0;
    }
</style>
<script>
import dayjs from 'dayjs';
import SearchSelect from '@/components/search-select';
import OssUpload from '@/components/upload';
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';
import { RoleEpType, EpStatus } from '@/libs/dic';

export default {
    name: 'CreditList',
    components: { SearchSelect, OssUpload },
    mixins: [ TableDataView, TableColRender, ModelView ],
    data () {
        return {
            statusStr: null,
            addressStr: null,
            officeAddressStr: null,
            licensePic: [],
            logoPic: [],
            showModelTitle: '审核',
            table: {
                col: [{
                    title: '编号',
                    key: 'id'
                }, {
                    title: '企业名称',
                    key: 'name'
                }, {
                    title: '管理员手机号',
                    key: 'adminPhone'
                }, {
                    title: '组织机构代码',
                    key: 'code'
                }, {
                    title: '用户类型',
                    key: 'type',
                    render: (h, params) => {
                        const rowData = params.row;
                        const showStr = rowData.epTypes === '4' ? '资源商' : rowData.epTypes === '5' ? '分发商' : rowData.epTypes === '0' ? '基裕' : '';
                        return h('span', showStr);
                    }
                }, {
                    title: '所在区域',
                    key: 'addressCode',
                    render: (h, params) => {
                        const rowData = params.row;
                        const showStr = (rowData.address === undefined ? '' : rowData.address) + (rowData.streetAddress === undefined ? '' : rowData.streetAddress);
                        return h('span', showStr);
                    }
                }, {
                    title: '状态',
                    key: 'status',
                    render: (h, params) => {
                        const rowData = params.row;
                        let showStr = '';
                        for (const i in EpStatus) {
                            if (EpStatus[i].id === rowData.status) {
                                showStr = EpStatus[i].name;
                            }
                        }
                        return h('span', showStr);
                    }
                }, {
                    title: '注册日期',
                    key: 'registerDate',
                    render: this.renderDate
                }, {
                    title: '操作',
                    key: 'action',
                    width: 200,
                    align: 'center',
                    render: (h, params) => this.renderActions(h, params, [
                        {
                            config: {
                                loading: this.loadingBtn,
                                style: { marginRight: '5px' }
                            },
                            click: () => this.showDetail(params.row),
                            text: '审核'
                        }
                    ])
                }],
                url: '/api/nmedia/role/list',
                query: {
                    name: null,
                    epType: `${RoleEpType[1].id}`,
                    isAudit: true
                }
            },
            vo: {
                id: null,
                code: null,
                name: null,
                legalPerson: null,
                licensePeriod: null,
                registerCapital: null,
                registerDate: null,
                businessScope: null,
                addressCode: null,
                address: null,
                streetAddress: null,
                officeAddressCode: null,
                officeAddress: null,
                officeStreetAddress: null,
                licensePic: null,
                logoPic: null,
                status: 1,
                user: {
                    username: null,
                    phone: null,
                    nickname: null
                },
                banking: {
                    realName: null,
                    cardNumber: null,
                    bankType: null,
                    alipayCode: null,
                    wechatCode: null
                },
                roles: [],
                epType: null,
                roleEntity: {
                    name: null
                }
            }
        };
    },
    methods: {
        showDetail (item) {
            this.model = true;
            this.vScrollToTop();
            Object.keys(this.vo).filter(key => Reflect.has(item, key)).forEach(key => {
                if (key === 'status') {
                    this.statusStr = item[key] === 1 ? '正常' : '禁用';
                } else if (key === 'address') {
                    this.addressStr = item[key];
                } else if (key === 'officeAddress') {
                    this.officeAddressStr = item[key];
                } else if (key === 'registerDate') {
                    this.vo[key] = dayjs(item[key]).toDate().toString();
                } else if (key === 'licensePic') {
                    this.licensePic = [{ name: item[key], status: true }];
                } else if (key === 'logoPic') {
                    this.logoPic = [{ name: item[key], status: true }];
                } else {
                    this.vo[key] = item[key];
                }
            });
            if (item.admin) {
                Object.keys(this.vo.user).filter(key => Reflect.has(item.admin, key)).forEach(key => this.vo.user[key] = item.admin[key]);
            }
            if (item.banking) {
                Object.keys(this.vo.banking).filter(key => Reflect.has(item.banking, key)).forEach(key => this.vo.banking[key] = item.banking[key]);
            }
            if (item.roleEntity) {
                Object.keys(this.vo.roleEntity).filter(key => Reflect.has(item.roleEntity, key)).forEach(key => this.vo.roleEntity[key] = item.roleEntity[key]);
            }
        },
        detailCancel () {
            this.logoPic = [];
            this.licensePic = [];
            Object.keys(this.vo).forEach(key => {
                if (key === 'status') {
                    this.vo[key] = 1;
                } else if (key === 'roles') {
                    this.vo[key] = [];
                } else if (key === 'user' || key === 'banking' || key === 'roleEntity') {
                    Object.keys(this.vo[key]).forEach(key1 => this.vo[key][key1] = null);
                } else {
                    this.vo[key] = null;
                }
            });
        },
        test () {
            this.$Message.info('lukas');
        },
        async pass () {
            const result = await this.fetch(`/api/nmedia/role/status/${this.vo.id}`, { method: 'post', params: { status: '1' } });
            if (result.success) {
                this.model = false;
                this.doQuery();
            }
        },
        async reject () {
            const result = await this.fetch(`/api/nmedia/role/status/${this.vo.id}`, { method: 'post', params: { status: '2' } });
            if (result.success) {
                this.model = false;
                this.doQuery();
            }
        }
    }
};
</script>

<style scoped>

</style>
